	module.exports = "<h2 id=overview>Overview</h2> <p>The navigation system relies on several different sub-components: </p> <ul> <li><code>.pf-c-nav__list</code> - Default navigation list. It is the basis for both default and expandable, vertical navigation.</li> <li><code>.pf-c-nav__simple-list</code> - Nav list simple is a shareable component this is used with grouped navigation and as secondary navigation.</li> <li><code>.pf-c-nav__horizontal-list</code> - Nav list horizontal is a shareable component that can be used within the page header, as primary navigation, or as tertiary navigation, when expandable, vertical navigation is implemented.</li> <li><code>.pf-c-nav__tertiary-list</code> - Nav list tertiary is a component that can be used within <code>&lt;main&gt;</code>, as third level navigation (tertiary navigation), when expandable, vertical navigation is implemented.</li> </ul> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>aria-label=&quot;[landmark description]&quot;</code></td> <td><code>.pf-c-nav</code></td> <td>Describes <code>&lt;nav&gt;</code> landmark</td> </tr> <tr> <td><code>aria-labelledby=&quot;[id value of link describing subnav]&quot;</code></td> <td><code>.pf-c-nav__subnav</code></td> <td>Gives the subnav <code>&lt;section&gt;</code> landmark an accessible name by referring to the element that provides the subnav <code>&lt;section&gt;</code> landmark title</td> </tr> <tr> <td><code>aria-expanded=&quot;false&quot;</code></td> <td><code>.pf-c-nav__link</code></td> <td>Indicates that subnav section is hidden</td> </tr> <tr> <td><code>aria-expanded=&quot;true&quot;</code></td> <td><code>.pf-c-nav__link</code></td> <td>Indicates that subnav section is visible</td> </tr> <tr> <td><code>hidden</code></td> <td><code>.pf-c-nav__subnav</code></td> <td>Indicates that the subnav section is hidden so that it isn&#39;t visible in the UI and isn&#39;t accessed by assistive technologies</td> </tr> <tr> <td><code>aria-current=&quot;page&quot;</code></td> <td><code>.pf-c-nav__link</code></td> <td>Indicates the current page link. Can only occur once on page.</td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-nav</code></td> <td><code>&lt;nav&gt;</code></td> <td>Initiates a primary nav element</td> </tr> <tr> <td><code>.pf-c-nav__list</code></td> <td><code>&lt;ul&gt;</code></td> <td>Initiates default nav list</td> </tr> <tr> <td><code>.pf-c-nav__item</code></td> <td><code>&lt;li&gt;</code></td> <td>Initiates default nav list item</td> </tr> <tr> <td><code>.pf-c-nav__link</code></td> <td><code>&lt;a&gt;</code></td> <td>Initiates default nav list link</td> </tr> <tr> <td><code>.pf-c-nav__section</code></td> <td><code>&lt;section&gt;</code></td> <td>Initiates a nav section element</td> </tr> <tr> <td><code>.pf-c-nav__section-title</code></td> <td><code>&lt;h1&gt;</code>, <code>&lt;h2&gt;</code>, <code>&lt;h3&gt;</code>, <code>&lt;h4&gt;</code>, <code>&lt;h5&gt;</code>, <code>&lt;h6&gt;</code></td> <td>Initiates a nav section title</td> </tr> <tr> <td><code>.pf-c-nav__toggle</code></td> <td><code>&lt;span&gt;</code></td> <td>Initiates a chevron indicating expandability of a <code>pf-c-nav__list-link</code></td> </tr> <tr> <td><code>.pf-m-expandable</code></td> <td><code>.pf-c-nav__item</code></td> <td>Modifies for the expandable state</td> </tr> <tr> <td><code>.pf-m-expanded</code></td> <td><code>.pf-c-nav__item</code></td> <td>Modifies for the expanded state</td> </tr> <tr> <td><code>.pf-m-hover</code></td> <td><code>.pf-c-nav__link</code></td> <td>Modifies to display the link as hovered</td> </tr> <tr> <td><code>.pf-m-focus</code></td> <td><code>.pf-c-nav__link</code></td> <td>Modifies to display the link as focussed</td> </tr> <tr> <td><code>.pf-m-current</code></td> <td><code>.pf-c-nav__link</code></td> <td>Modifies for the current state</td> </tr> <tr> <td><code>.pf-m-active</code></td> <td><code>.pf-c-nav__link</code>,</td> <td>Modifies to display the link as active</td> </tr> <tr> <td><code>.pf-m-disabled</code></td> <td><code>.pf-c-nav__link</code>,</td> <td>Modifies to display the link as disabled</td> </tr> </tbody> </table> ";

